.feature {
  background-color: rgba($color-white, .8);
  font-size: 1.5rem;
  padding: 2.5rem;
  border-radius: 3px;
  text-align: center;
  box-shadow: 0 1.5rem 4rem rgba($color-black, .15);
  transition: transform .3s;

  &:hover {
    transform: translateY(-1.5rem) scale(1.03);
  }

  &__icon {
    font-size: 6rem;
    margin-bottom: .5rem;
    color: transparent;
    background-image: linear-gradient(to right, $color-lgreen, $color-dgreen);
    background-clip: text;
    -webkit-background-clip: text;
  }
}